module.exports = "body, html{\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: 'Helvetica', sans-serif;\r\n    overflow: hidden;\r\n}\r\n\r\nh1, h2, h3, h4, h5, h6, p {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\ninput[type=\"number\"]{\r\n    width: 40px;\r\n    text-align: right;\r\n}\r\n\r\n/*  Angular draggable/resizable handle */\r\n\r\n.ng-resizable-diagonal {\r\n    display: none;\r\n}\r\n\r\n/* Alignment Classes */\r\n\r\n.wrap {\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.flex-row-start {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: flex-start;\r\n    align-items: center;\r\n}\r\n\r\n.flex-row-evenly {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-evenly;\r\n    align-items: center;\r\n}\r\n\r\n.flex-row-between {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.flex-col-start {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: flex-start;\r\n    align-items: center;\r\n}\r\n\r\n.flex-col-evenly {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-evenly;\r\n    align-items: center;\r\n}\r\n\r\n.flex-col-between {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n/* Color Scheme Classes */\r\n\r\n.accent01{\r\n    background: #040303;\r\n    color: #BEB0A7;\r\n}\r\n\r\n.accent02{\r\n    background: #3A4E48;\r\n    color: rgb(235, 216, 205);\r\n}\r\n\r\n.accent03{\r\n    background: #6A7B76;\r\n    color: #d8d8d8;\r\n}\r\n\r\n.accent04{\r\n    background: #8B9D83;\r\n    color: #040303;\r\n}\r\n\r\n.accent05{\r\n    background: #BEB0A7;\r\n    color: #3A4E48;\r\n}\r\n\r\n.object-icon {\r\n    padding: 5px;\r\n    border-radius: 5px;\r\n    background: none;\r\n    border: 3px silver dotted\r\n}\r\n\r\na:hover {\r\n    cursor: pointer;\r\n}\r\n\r\n/*-----*/\r\n\r\n.greenAccent01{\r\n    background: #60695C;\r\n    color: #D6F9DD;\r\n}\r\n\r\n.greenAccent02{\r\n    background: rgb(63, 78, 66);\r\n    color: rgb(255, 209, 82)\r\n}\r\n\r\n.blueAccent01{\r\n    background: rgb(56, 23, 243);\r\n    color: rgb(255, 255, 255);\r\n}\r\n\r\n.blueAccent02{\r\n    background: rgb(73, 85, 73);\r\n    color: rgb(204, 235, 204)\r\n}\r\n\r\n.grayAccent01{\r\n    background: rgb(34, 34, 34);\r\n    color: rgb(255, 255, 255)\r\n}\r\n\r\n.grayAccent02{\r\n    background: rgb(65, 65, 65);\r\n    color: rgb(231, 231, 231)\r\n}\r\n\r\n.grayAccent03{\r\n    background: rgb(221, 221, 221);\r\n    color: rgb(99, 99, 99)\r\n}\r\n\r\n/* Large toolbar buttons */\r\n\r\n.lg-btn {\r\n    font-size: 0.7rem;\r\n    text-align: center;\r\n    margin: 0;\r\n    padding: 0;\r\n    height: 100px;\r\n    width: 125px;\r\n    min-width: 100px;\r\n}\r\n\r\n.lg-btn:hover {\r\n    background: rgb(82, 82, 82);\r\n}\r\n\r\n.lg-btn p {\r\n    margin: 0 10px;\r\n}\r\n\r\n/* Medium toolbar buttons */\r\n\r\n.md-btn {\r\n    font-size: 0.7rem;\r\n    text-align: center;\r\n    margin: 0;\r\n    padding: 0 20px;\r\n    height: 75px;\r\n}\r\n\r\n.md-btn:hover {\r\n    background: rgb(82, 82, 82);\r\n}\r\n\r\n.md-btn p {\r\n    max-width: 75px;\r\n}\r\n\r\n.dotted-border{\r\n    border: 1px silver dashed\r\n}\r\n\r\n/* Small toolbar buttons */\r\n\r\n.sm-btn {\r\n    font-size: 0.7rem;\r\n    text-align: center;\r\n    margin: 0;\r\n    padding: 0 5px;\r\n    height: 30px;\r\n}\r\n\r\n.sm-btn fa {\r\n    margin: 0 5px;\r\n}\r\n\r\n.sm-btn:hover {\r\n    background: rgb(82, 82, 82);\r\n}\r\n\r\n/* Extra small buttons */\r\n\r\n.xs-btn{\r\n    font-size: 0.7rem;\r\n    text-align: center;\r\n    margin: 0 auto;\r\n    padding: 0;\r\n    height: auto;\r\n}\r\n\r\n/* Color Selectors */\r\n\r\n.md-color-selector{\r\n    width: 100px;\r\n    height: 10px;\r\n    border: 0;\r\n}\r\n\r\n"